@use 'base/variables' as v;
@use 'base/mixins' as m;

.creation {
    padding: 5rem 0;
    &__grid {
        @include m.tablet { 
            @include m.grid(2, 2rem);
            align-items: center;
        }
    }
    &__heading {
        @include m.borde(v.$secundario, v.$primario);
    }
}